import Info from "./Info";
import Bringt from "./bringt";
import { ErweiterungType } from "./erweiterung";
import Kosten from "./kosten";
import { BuildingTags } from "./tags";

class Building {
  kosten: Kosten;
  erweiterung: ErweiterungType;
  bringt: Bringt;
  braucht: Kosten;
  kraft: number;
  info: Info;
  abkrzung?: string;
  tags: BuildingTags[];
  //TODO einheiten

  constructor(
    kosten: Kosten,
    erweiterung: ErweiterungType,
    bringt: Bringt,
    kraft: number,
    info: Info,
    tags: BuildingTags[],
    braucht: Kosten,
    abkrzung?: string
  ) {
    this.kosten = kosten;
    this.erweiterung = erweiterung;
    this.bringt = bringt;
    this.kraft = kraft;
    this.info = info;
    this.tags = tags;
    this.braucht = braucht;
  }
}

export default Building;
