import { plainToInstance } from "class-transformer";
import { Einheit } from "../../types/einheit";

let Engel = require("./Engel.json");
let Knig = require("./Knig.json");
let Drachenknig = require("./Drachenknig.json");
let Meeresjnger = require("./Meeresjnger.json");
let Meeresknig = require("./Meeresknig.json");
let Zauberer = require("./Zauberer.json");
let Orks = require("./Orks.json");
let Palastwache = require("./Palastwache des Zwergenknigs.json");
let Kobolde = require("./Kobolde.json");
export const fetchUnits = () => {
  return [
    plainToInstance(Einheit, Engel),
    plainToInstance(Einheit, Knig),
    plainToInstance(Einheit, Drachenknig),
    plainToInstance(Einheit, Meeresjnger),
    plainToInstance(Einheit, Meeresknig),
    plainToInstance(Einheit, Zauberer),
    plainToInstance(Einheit, Orks),
    plainToInstance(Einheit, Palastwache),
    plainToInstance(Einheit, Kobolde),
  ];
};
