import Info from "./Info";
import Bewegung from "./bewergung";
import Effekt from "./effekt";
import Kosten from "./kosten";
import Transport from "./transport";
import { UnitTags } from "./tags";

export class Einheit {
    kosten: Kosten;
    abkrzung: string;
    bewegung: Bewegung;
    kraft: number;
    tags: UnitTags[];
    effekte: Effekt[];
    info: Info;
    transport: Transport

    constructor(kosten: Kosten, info: Info, abkrzung: string, bewegung: Bewegung, kraft: number, tags: UnitTags[], effekte: Effekt[], transport: Transport) {
        this.kosten = kosten;
        this.abkrzung = abkrzung;
        this.bewegung = bewegung;
        this.kraft = kraft;
        this.tags = tags;
        this.effekte = effekte;
        this.info = info;
        this.transport = transport;
    }
}